import './App.css';
import {
    Button
} from '@material-ui/core';
import {HeaderStyle} from "./styles";
import {useEffect, useState} from "react";
import React from "react"
import image from './image.png'


const array_yes = [
    'ДА!!!',
    'YEAAAAH!!!',
    'Конечно)',
    'АГАСЬ',
    'Дааааа!!!'
]

const baybach = [
    'Ты за BAYBACH-CITY???',
    'Кирилл без ума от BAYBACH-CITY, а ты?',
    'Ты любишь BAYBACH-CITY??? ОТВЕЧАЙ, БЫСССТРО!!!',
    'Если ты не за BAYBACH-CITY - пошел вон! Если за, нажми кнопку!',
    'BAYBACH-CITY лучшие?',
    'BAYBACH-CITY лучшая команда в мире???'
]

const array = [
    'Ты выбрал правильную сторону! Так держать!',
    'Я уж думал что ты в нас начал сомневаться)',
    'За честь и отвагу!!!',
    'Оп давай-давай, оп давай-давай, BAYBACH-CITY побеждай',
    'Талант выигрывает игры, а команда BAYBACH-CITY — чемпионаты',
    'Только в BAYBACH-CITY вы обретаете себя!',
    'Где BAYBACH-CITY там всегда победа))',
    'Нет ничего лучше, чем BAYBACH-CITY, сынок ^)',
    'Красава! А теперь гони баблишко, мой новый спонсор :)',
    'Мох! Стать похожим меньше на цветок, Больше — на мох! Японский сад промок, Воду пьёт зелёный мох',
    'Если ты Матвей, я помню, что ты играл за другую команду, поэтому выйди, неверный(',
]

function App() {
    const [state, setState] = useState(false)
    const [text, setText] = useState('')
    const [textYes, setTextYes] = useState('')
    const [textbaybach, setTextBaybach] = useState('')
    const [num, setNum] = useState(true)

    useEffect(() => {
        setTextYes(array_yes[Math.floor(Math.random() * array_yes.length)])
        setTextBaybach(baybach[Math.floor(Math.random() * baybach.length)])
    }, [])

    const classes = HeaderStyle()

    const getAnswer = () => {
        setState(!state)
        let ch = Math.floor(Math.random() * array.length)
        setText(array[ch])
        if (ch === 9) {
            setNum(false)
        }
    }
    return (
        <div className={classes.app}>
            <div className={state ? classes.main2 : classes.main}>
                {state
                    ?
                    <React.Fragment>
                        <div className={classes.text}>
                            {text}
                        </div>

                        <img className='App-logo-spin App-logo' src={image} alt="image.jpg"/>
                        <audio src={num ? 'GTA.mp3' : 'Mox.mp3'} controls>
                        </audio>

                    </React.Fragment>
                    :

                    <React.Fragment>
                        <div className={classes.text}>
                            {textbaybach}
                        </div>
                        <Button onClick={() => getAnswer()} variant="outlined" className={classes.locale}>
                            {textYes}
                        </Button>
                    </React.Fragment>
                }

            </div>
        <p className='site'>Сreated by Tomilin</p>
        </div>
    );
}

export default App;
